import React from "react";
import _ from "lodash";
import ReactGridLayout, { Responsive } from "react-grid-layout";
import "./Toolbox.css";
import WidthProvider from "./WidthProvider"


export const breakpoints = {lg: 1200, md: 996, sm: 768, xs: 480, xxs: 240}
var ResponsiveReactGridLayout
export default class ToolboxLayout extends React.Component {
  static defaultProps = {
    className: "layout",
    rowHeight: 30,
    onLayoutChange: function () {},
    cols: { lg: 12, md: 10, sm: 6, xs: 4, xxs: 2 },
    initialLayout: generateLayout(),
  };

  constructor(props) {
    super(props);
    ResponsiveReactGridLayout = WidthProvider(Responsive,props.fixedWidth);
    this.state = {
      currentBreakpoint: "lg",
      compactType: "vertical",
      mounted: false,
      // layouts: props.layouts
      //   ? props.layouts
      //   : {
      //       lg: props.tools
      //         ? props.tools.map((tool, ix) => {
      //             return {
      //               x: 0,
      //               y: ix,
      //               w: 3,
      //               h: 3,

      //               i: ix.toString(),

      //               static: 0, //Math.random() < 0.05,
      //             };
      //           })
      //         : props.initialLayout,
      //     },
      toolbox: { lg: [] },
    };
  }

  componentDidMount() {
    this.setState({ mounted: true });
  }

  generateDOM(x,y) {
    var that = this;
    
    return _.map(this.props.layouts[this.state.currentBreakpoint], (l) => {
      
      var tile =
        that.props.tools && that.props.tools[l.i] && that.props.tools[l.i].tile
          ? that.props.tools[l.i].tile
          : { title: l.i };
          {var refresh = Date.now()}
      return (
        <div
          style={{
            backgroundColor: tile.color,
            color: tile.textcolor,
            cursor: !this.props.editingLayout ? "pointer" : "default",
            overflow:"hidden"
          }}
          // onClick={() => {
          //   if (this.props.onTileClick && !this.props.editingLayout) {
          //     this.props.onTileClick(tile);
          //   }
          // }}
          key={l.i}

          data-grid={{w:l.w,h:l.h,x:l.x,y:l.y}}
          className={l.static ? "static" : ""}
        >
       
          <span >
          
        {/* {refresh} {l.w}x{l.h} */}
            {that.props.getPin(l.i,l.w,l.h,refresh)} 
            </span>
        </div>
      );
    });
  }

  onBreakpointChange = (breakpoint) => {
    this.setState((prevState) => ({
      currentBreakpoint: breakpoint,
      toolbox: {
        ...prevState.toolbox,
        [breakpoint]:
          prevState.toolbox[breakpoint] ||
          prevState.toolbox[prevState.currentBreakpoint] ||
          [],
      },
    }));
  };

  onCompactTypeChange = () => {
    const { compactType: oldCompactType } = this.state;
    const compactType =
      oldCompactType === "horizontal"
        ? "vertical"
        : oldCompactType === "vertical"
        ? null
        : "horizontal";
    this.setState({ compactType });
  };

  onTakeItem = (item) => {
    this.setState((prevState) => ({
      toolbox: {
        ...prevState.toolbox,
        [prevState.currentBreakpoint]: prevState.toolbox[
          prevState.currentBreakpoint
        ].filter(({ i }) => i !== item.i),
      },
      layouts: {
        ...prevState.layouts,
        [prevState.currentBreakpoint]: [
          ...prevState.layouts[prevState.currentBreakpoint],
          item,
        ],
      },
    }));
  };

  onPutItem = (item) => {
    this.setState((prevState) => {
      return {
        toolbox: {
          ...prevState.toolbox,
          [prevState.currentBreakpoint]: [
            ...(prevState.toolbox[prevState.currentBreakpoint] || []),
            item,
          ],
        },
        layouts: {
          ...prevState.layouts,
          [prevState.currentBreakpoint]: prevState.layouts[
            prevState.currentBreakpoint
          ].filter(({ i }) => i !== item.i),
        },
      };
    });
  };

  onLayoutChange = (layout, layouts) => {
    saveToLS("layouts", layouts);

    if (this.props.persist) {
      this.props.persist(layouts);
    }
    this.setState({ layouts });
  };

  onNewLayout = () => {
    this.setState({
      layouts: { lg: generateLayout() },
    });
  };
  onResize = () =>{
    if (!this.props.tools) return
    
    this.props.tools.forEach(tool => {
      if (tool.onResize) tool.onResize()
    });

  }
  render() {
    return (
      <div>
        {/* <div>
          Current Breakpoint: {this.state.currentBreakpoint} (
          {this.props.cols[this.state.currentBreakpoint]} columns)
        </div>
        <div>
          Compaction type:{" "}
          {_.capitalize(this.state.compactType) || "No Compaction"}
        </div>
        <button onClick={this.onNewLayout}>Generate New Layout</button>
        <button onClick={this.onCompactTypeChange}>
          Change Compaction Type
        </button> */}

        {/* <ToolBox
          items={this.state.toolbox[this.state.currentBreakpoint] || []}
          onTakeItem={this.onTakeItem}
        /> */}

        <ResponsiveReactGridLayout
       
         breakpoints={breakpoints }
         cols={{lg: 12, md: 10, sm: 6, xs: 4, xxs: 2}}
          {...this.props}
          
          layouts={this.props.layouts}
          onBreakpointChange={this.onBreakpointChange}
          onLayoutChange={this.onLayoutChange}
          // WidthProvider option
          measureBeforeMount={false}
          // I like to have it animate on mount. If you don't, delete `useCSSTransforms` (it's default `true`)
          // and set `measureBeforeMount={true}`.
          useCSSTransforms={true}
          compactType={this.state.compactType}
          preventCollision={!this.state.compactType}
          onResize={this.onResize}
          isDraggable={this.props.editMode}
          isResizable={this.props.editMode}
        >
          {this.generateDOM(this)}
        </ResponsiveReactGridLayout>
      </div>
    );
  }
}

function getFromLS(key) {
  let ls = {};
  if (global.localStorage) {
    try {
      ls = JSON.parse(global.localStorage.getItem("rgl-8")) || {};
    } catch (e) {
      /*Ignore*/
    }
  }
  return ls[key];
}

function saveToLS(key, value) {
  if (global.localStorage) {
    global.localStorage.setItem(
      "rgl-8",
      JSON.stringify({
        [key]: value,
      })
    );
  }
}
function generateLayout() {
  return _.map(_.range(0, 25), function (item, i) {
    var y = Math.ceil(Math.random() * 4) + 1;
    return {
      x: (_.random(0, 5) * 2) % 12,
      y: Math.floor(i / 6) * y,
      w: 2,
      h: y,
      i: i.toString(),
      static: Math.random() < 0.05,
    };
  });
}
