import React, { useEffect, useState, useRef } from "react";
import { Dialog, DialogType, DialogFooter } from 'office-ui-fabric-react/lib/Dialog';
import { PrimaryButton, DefaultButton } from 'office-ui-fabric-react/lib/Button';
import { hiddenContentStyle, mergeStyles } from 'office-ui-fabric-react/lib/Styling';
import { Toggle } from 'office-ui-fabric-react/lib/Toggle';
import { ContextualMenu } from 'office-ui-fabric-react/lib/ContextualMenu';
import { useId, useBoolean } from '@uifabric/react-hooks';
import { TextField } from "@fluentui/react";

const dialogStyles = { main: { maxWidth: 450 } };
const dragOptions = {
  moveMenuItemText: 'Move',
  closeMenuItemText: 'Close',
  menu: ContextualMenu,
};
const screenReaderOnly = mergeStyles(hiddenContentStyle);
const dialogContentProps = {
  type: DialogType.normal,
  title: 'Name your board',
  closeButtonAriaLabel: 'Close',
  subText: 'Give your board a descriptive name, use 30-40 characters if you like',
};

export default function AddBoard(props) {
    const [hideDialog, setHideDialog] = useState(false);
    const [name, setName] = useState("");
  const labelId = useId('dialogLabel');
  const subTextId = useId('subTextLabel');



  return (
    <>

      <Dialog
        hidden={hideDialog}
        onDismiss={()=>{setHideDialog(true)}}
        dialogContentProps={dialogContentProps}
      
      >
       <TextField
                    autoFocus={false}
                 
                    label="Name"
                    text={name}
                    onChange={(e, newValue) => {
                     setName(newValue)
                    }}
                    placeholder=""
                    xiconProps={{ iconName: "Filter" }}
                  />
        <DialogFooter>
          <PrimaryButton  text="Save"  disabled={name===""} onClick={()=>{
              setHideDialog(true)
              if (props.onAdd){

                props.onAdd(name)
              }
          }}/>
          <DefaultButton  text="Cancel" onClick={()=>{
              setHideDialog(true)
              if ( props.onDismiss)

                props.onDismiss()
              }
          }
          
          />
        </DialogFooter>
      </Dialog>
    </>
  );
};



