graph.getSharePointList = (
  token,
  host,
  site,
  list,
  listfilter,
  itemfilter,
  cb
) => {
  get(
    token,
    `https://graph.microsoft.com/v1.0/sites/${host}:${site}:/lists/${list}/items?${listfilter}`,
    function (e, items) {
      if (e) return cb(e);
      var itemList = [];

      function next() {
        if (items.length === 0) return cb(null, itemList);
        var item = items.pop();

        get(
          token,
          `https://graph.microsoft.com/v1.0/sites/${host}:${site}:/lists/${list}/items/${item.id}?${itemfilter}`,
          function (e, item) {
            if (e) return cb(e);
            itemList.push(item);
            next();
          }
        );
      }

      next();
    }
  );
};

graph.getPages = (token, host, site, cb) => {
  get(
    token,
    `https://graph.microsoft.com/v1.0/sites/${host}:${site}`,
    function (e, site) {
      if (e) return cb(e);

      get(
        token,
        `https://graph.microsoft.com/beta/sites/${site.id}/pages`,
        function (e, items) {
          if (e) return cb(e);

          return cb(null, items);
        }
      );
    }
  );
};
