graph.copyfile = function (
  token,
  groupNickName,
  fromFolder,
  fromName,
  toFolder,
  toName,
  cb
) {
  graph.findGroup(token, groupNickName, function (err, groups) {
    if (err) return cb(err);
    if (groups.length !== 1) return cb("Did't find a unique group");
    var group = groups[0];
    graph.getGroupDocuments(token, group.id, "/" + fromFolder, function (
      err,
      fromItems
    ) {
      if (err) return cb(err);
      var match = _.find(fromItems, { name: fromName });
      graph.getGroupRootFolders(token, group.id, function (err, targetFolders) {
        if (err) return cb(err);
        var destFolder = _.find(targetFolders, { name: toFolder });
        if (!destFolder) return cb("Did't find target folder");
        graph.newDoc(
          token,
          group.id,
          match.id,
          destFolder.parentReference.driveId,
          destFolder.id,
          toName,
          function (err, result) {
            if (err) return cb(err);
            return cb(null, result);
          }
        );
      });
    });
  });
};

function lookupFile(token, driveId, id, name, iterations, cb) {
  // Content have been copied
  graph.getDriveChildren(token, driveId, id, function (err, result) {
    if (err) {
      return cb(err);
    }
    // content of destination folder

    for (let index = 0; index < result.length; index++) {
      const file = result[index];

      console.log("Comparing", file.name, name);
      if (file.name === name) {
        // match found
        console.log("Match found");
        return cb(null, file);
      }
    }

    var iteration = 0;
    function retry() {
      console.log("Retrying", iterations);
      //
      if (iteration > 10) {
        return cb(
          `Think that the file has been created, but could not find a reference to if. Filename is ${name} - Tryed ${iterations} to find it `
        );
      }
      lookupFile(token, driveId, id, name, iterations + 1, cb);
    }
    // no match found so we will wait a second a try again
    setTimeout(retry, 1000);
  });
}

graph.newDoc = function (token, sourceGroupId, itemId, driveId, id, name, cb) {
  post(
    token,
    `https://graph.microsoft.com/beta/groups/${sourceGroupId}/drive/items/${itemId}/copy`,
    {
      parentReference: {
        driveId: driveId,
        id: id,
      },
      name: name,
    },
    function (err) {
      // no result from that call - by design from Microsoft
      if (err) {
        return cb(err);
      }

      // so we fill try to find a referece our self
      lookupFile(token, driveId, id, name, 0, cb);
    }
  );
};
export const graphWithPromise = {};
graphWithPromise.me = (token) => {
  return new Promise((resolve, reject) => {
    get(
      token,
      "https://graph.microsoft.com/v1.0/me/",
      (error, result) => {
        if (error) {
          return reject(error);
        }
        return resolve(result);
      },
      null
    );
  });
};

class Client {
  constructor() {
    this.token = auth.getAccessToken();
  }
  get = (url,options) => {
    return new Promise((resolve, reject) => {
      get(this.token, url, (error, result) => {
        if (error) {
          return reject(error);
        }
        return resolve(result);
      }, null,null,null,options);
    });
  };

  post = (url, payload) => {
    return new Promise((resolve, reject) => {
      post(this.token, url, payload, (error, result) => {
        if (error) {
          return reject(error);
        }
        return resolve(result);
      });
    });
  };

  patch = (url, payload) => {
    return new Promise((resolve, reject) => {
      patch(this.token, url, payload, (error, result) => {
        if (error) {
          return reject(error);
        }
        return resolve(result);
      });
    });
  };
  put = (url, payload) => {
    return new Promise((resolve, reject) => {
      put(this.token, url, payload, (error, result) => {
        if (error) {
          return reject(error);
        }
        return resolve(result);
      });
    });
  };
  getRaw = (url) => {
    return getRaw(this.token, url);
  };
  getImage = (url) => {
    return getImage(this.token, url);
  };
}

function imageToBase64(blob) {
  return new Promise((resolve, reject) => {
    try {
      var reader = new FileReader();

      reader.onload = function () {
        var blobAsDataUrl = reader.result;

        resolve(blobAsDataUrl);
      };

      reader.readAsDataURL(new Blob([blob]));
    } catch (error) {
      debugger;
      resolve(null);
    }
  });
}
const get2 = (url,options) => {
  var client = new Client();
  return client.get(url,options);
};

const patch2 = (url, payload) => {
  var client = new Client();
  return client.patch(url, payload);
};
const post2 = (url, payload) => {
  var client = new Client();
  return client.post(url, payload);
};
const put2 = (url, payload) => {
  var client = new Client();
  return client.put(url, payload);
};
