export const getOneDriveFile = (folderNameWithOutInitialSlash, filename) => {
  return new Promise(async (resolve, reject) => {
    var folder = await getOneDriveFolder(folderNameWithOutInitialSlash).catch(
      (e) => {
        reject(e);
      }
    );
    if (!folder) return;

    get2(
      `https://graph.microsoft.com/v1.0/me/drive/items/${folder.id}:/${filename}`
    )
      .then((file) => {
        axios
          .get(file["@microsoft.graph.downloadUrl"])
          .then((response) => {
            resolve(response.data);
          })
          .catch((e) => {
            reject(e);
          });
      })
      .catch((e) => {
        reject(e);
      });
  });
};

export const saveOneDriveFile = (
  folderNameWithOutInitialSlash,
  filename,
  payload
) => {
  return new Promise(async (resolve, reject) => {
    console.log("Size of payload", payload.length);
    var folder = await getOneDriveFolder(folderNameWithOutInitialSlash).catch(
      (e) => {
        reject(e);
      }
    );
    if (!folder) return;

    put2(
      `https://graph.microsoft.com/v1.0/me/drive/items/${folder.id}:/${filename}:/content`,
      payload
    )
      .then((result) => {
        resolve(result);
      })
      .catch((e) => {
        reject(e);
      });
  });
};

