export const saveMyExtention = (extensionName, data) => {
  return new Promise(async (resolve, reject) => {
    var existing = await getMyExtention(extensionName).catch((e) => {});
    if (!existing) {
      addMyExtention(extensionName, data)
        .then((r) => {
          resolve(r);
        })
        .catch((e) => {
          reject(e);
        });
    } else {
      updateMyExtention(extensionName, data)
        .then((r) => {
          resolve(r);
        })
        .catch((e) => {
          reject(e);
        });
    }
  });
};
export const updateMyExtention = (extensionName, data) => {
  var payload = {
    "@odata.type": "#microsoft.graph.openTypeExtension",
    extensionName: extensionName,
    ...data,
  };
  return patch2(
    `https://graph.microsoft.com/v1.0/me/extensions/${extensionName}`,
    payload
  );
};
export const addMyExtention = (extensionName, data) => {
  var payload = {
    "@odata.type": "#microsoft.graph.openTypeExtension",
    extensionName: extensionName,
    ...data,
  };
  return post2(`https://graph.microsoft.com/v1.0/me/extensions`, payload);
};

export const teamMemberships = (reportProgessPercent) => {
  var client = new Client();
  return new Promise(async (resolve, reject) => {
    try {
      var memberships = await client
        .get("https://graph.microsoft.com/v1.0/me/memberOf")
        .catch((error) => reject(error));
      var pending = 0;
      var groups = [];

      if (!memberships) return reject("Not able to read membership");

      //memberships = [memberships[0]];
      var total = memberships.length;
      var processed = 0;
      if (reportProgessPercent) reportProgessPercent(0);
      memberships.map(async (value) => {
        pending++;

        var group = await readGroup(value, client);

        processed++;
        if (reportProgessPercent) reportProgessPercent(processed / total);
        groups.push(group);

        pending--;
        if (pending === 0) {
          return resolve(
            groups.sort((a, b) => {
              if (a.text < b.text) {
                return -1;
              }
              if (a.text > b.text) {
                return 1;
              }
              return 0;
            })
          );
        }
      });
    } catch (error) {
      debugger;
      reject(error);
    }
  }, null);
};

export var auth = {};

auth.getAccessToken = function () {
  return sessionStorage.getItem("access_token");
};

function readGroup(value, client) {
  return new Promise(async (resolve, reject) => {
    var group = {
      ...value,
      key: value.id,
      text: value.title ? value.title : value.displayName,
      title: value.description,
    };
    var photo = await client
      .getImage(
        `https://graph.microsoft.com/v1.0/groups/${value.id}/photo/$value`
      )
      .catch((error) => {
        debugger;
        console.log("Photo read error");
      });
    if (photo && photo.indexOf("data:application/json") > -1) photo = null;

    var details = await client
      .get(`https://graph.microsoft.com/v1.0/teams/${value.id}`)
      .catch((error) => console.log("Team details read error"));
    (value.creationOptions ? value.creationOptions : []).forEach((v) => {
      if (v === "YammerProvisioning") group.hasYammer = true;
      if (v === "ProvisionGroupHomepage") group.hasSharePoint = true;
    });
    (value.resourceProvisioningOptions
      ? value.resourceProvisioningOptions
      : []
    ).forEach((v) => {
      if (v === "YammerProvisioning") group.hasYammer = true;
      if (v === "ProvisionGroupHomepage") group.hasSharePoint = true;
    });
    
    // https://www.techmikael.com/2017/11/retrieving-teams-and-yammer-endpoints.html
    var channels = await readChannels(client, value).catch((e) => {});
    var endPoints = await readEndpoints(client, value).catch((e) => {});
    group.hasTeams = channels !== undefined;
    group.data = {
      endPoints,
      details,
      photo,
      channels: channels,
    };
    resolve(group);
  });
}

async function readChannels(client, value) {
  var queue = 0;
  return new Promise(async (resolve, reject) => {
    var channels = await client
      .get(`https://graph.microsoft.com/beta/teams/${value.id}/channels`)
      .catch((error) => {
        console.log("Channel read error");
      });
    if (!channels) return resolve();

    channels.forEach(async (channel) => {
      queue++;
      var tabs = await client
        .get(
          `https://graph.microsoft.com/beta/teams/${value.id}/channels/${channel.id}/tabs`
        )
        .catch((error) => {
          console.log("Channel read tabs", error);
        });

      channel.tabs = tabs;
      queue--;
      if (queue === 0) {
        resolve(channels);
      }
    });
  });
}

async function readEndpoints(client, value) {
  return client.get(
    `https://graph.microsoft.com/beta/groups/${value.id}/endpoints`
  );
}

function post(token, url, body, cb) {
  axios
    .post(url, body, {
      headers: {
        "content-type": "application/json",
        authorization: "Bearer " + token,
      },
    })
    .then((result) => {
      cb(null, result.data);
    })
    .catch((e) => {
      cb(e);
    });
}

function patch(token, url, body, cb) {
  axios
    .patch(url, body, {
      headers: {
        "content-type": "application/json",
        authorization: "Bearer " + token,
      },
    })
    .then((result) => {
      cb(null, result.data);
    })
    .catch((e) => {
      cb(e);
    });
}

function put(token, url, body, cb) {
  axios
    .put(url, body, {
      headers: {
        "content-type": "application/json",
        authorization: "Bearer " + token,
      },
    })
    .then((result) => {
      cb(null, result.data);
    })
    .catch((e) => {
      cb(e);
    });
}
export function getRaw(token, url) {
  console.log("getting", url);

  return new Promise((resolve, reject) => {
    request.get(
      {
        url: url,
        headers: {
          authorization: "Bearer " + token,
        },
      },
      function (err, response, body) {
        if (err) {
          return reject(err);
        }
        resolve({ body, response });
      }
    );
  });
}

function getImage(token, url) {
  return new Promise((resolve, reject) => {
    try {
      var xhr = new XMLHttpRequest();

      xhr.onload = function () {
        var reader = new FileReader();
        reader.onloadend = function () {
          resolve(reader.result);
        };
        reader.readAsDataURL(xhr.response);
      };

      xhr.open("GET", url);
      xhr.setRequestHeader("Authorization", "Bearer " + token);
      xhr.responseType = "blob";
      xhr.send();
    } catch (error) {
      reject(error);
    }
  });
}

export function get(token, url, cb, appendTo, index, retrycount,options) {
  console.log("getting", url, appendTo === true);
  request.get(
    {
      url: url,
      headers: {
        "content-type": "application/json",
        authorization: "Bearer " + token,
      },
    },
    function (err, response, body) {
      var parsedBody;

      if (err) {
        var errCounter = retrycount ? retrycount + 1 : 1;
        if (errCounter > 3) {
          return cb(err, null, index);
        } else {
          // retry
          console.log("retry #", errCounter);
          return get(token, url, cb, appendTo, index, errCounter);
        }
      }

      parsedBody = JSON.parse(body);
      if (parsedBody.error) {
        return cb(parsedBody.error, null, index);
      }

    
      if (parsedBody["@odata.nextLink"]) {
        
        var followNextLink = options && options.skipNextLink ? false:true 
        var data;
        if (!appendTo) {
          data = parsedBody.value;
        } else {
          data = appendTo.concat(parsedBody.value);
        }
        if (followNextLink){
        return get(token, parsedBody["@odata.nextLink"], cb, data, index);
      }
      }

      if (appendTo) {
        cb(null, appendTo.concat(parsedBody.value), index);
      } else {
        if (parsedBody.value) {
          cb(null, parsedBody.value, index);
        } else {
          cb(null, parsedBody, index);
        }
      }
    }
  );
}
