import React, {
  Component,
  useState,
  useEffect,
  useLayoutEffect,
  useRef,
} from "react";
import PropTypes from "prop-types";
import { Highlight } from "./Highlight";
import Masonry from "react-masonry-css";
import moment from "moment";
import "./masonry.css";
import { GroupMouseOver, PinMouseOver } from "./MouseOver";
import _ from "lodash";
import { EVENT, trace } from "../helpers/trace";

import { getHref, teamsLink, teamsLink2 } from "../helpers/LaunchPad";
import "./tile.css";
import { navigate } from "@reach/router";
import { ActionButton, Button, IconButton } from "@fluentui/react";
import { readTeamChannelMessages } from "../helpers/OfficeGraph";
const breakpointColumnsObj = {
  default: 11,

  3600: 11,

  3300: 10,
  3000: 9,
  2700: 8,
  2400: 7,

  2100: 6,
  1800: 5,
  1500: 4,
  1200: 3,
  900: 2,
  600: 1,
  //300: 1,
};
export const useContainerDimensions = (myRef) => {
  const getDimensions = () => ({
    width: myRef.current.offsetParent.offsetWidth,
    height: myRef.current.offsetParent.offsetHeight,
  });

  const [dimensions, setDimensions] = useState({ width: 0, height: 0 });

  useEffect(() => {
    if (myRef.current) {
      setDimensions(getDimensions());
    }
  }, []);

  return dimensions;
};
export const ViewTeams = (props) => {
  const [viewChannels, setViewChannels] = useState(props.details);
  const [viewDates, setViewDates] = useState(false);

  if (props && props.memberShips) {
    var memberShips = _.cloneDeep(props.memberShips);

    if (props.tiles) {
      props.tiles.forEach((tile) => {
        //debugger
        memberShips.push({
          displayName: tile.title,
          tile,
        });
      });

      memberShips = _.sortBy(memberShips, ["displayName"]);
    }
    return (
      <div
        className="tags"
        style={{
          padding: "4px",
          margin: "4px",
          fontSize: "16px",
          display: "flex",
          flexWrap: "wrap",
          minWidth: "300px",
        }}
      >
        <Masonry
          breakpointCols={breakpointColumnsObj}
          className="my-masonry-grid"
          columnClassName="my-masonry-grid_column"
        >
          {/* array of JSX items */}

          {memberShips.map((team, key) => {
            //    if ((!team.resourceProvisioningOptions || !team.resourceProvisioningOptions.length > 0) && (team.creationOptions && !team.creationOptions.length > 0)) return null
            //if ( team["@odata.type"] === "#microsoft.graph.directoryRole") return null
            // Profoe: "https://365adm.sharepoint.com/_layouts/groupstatus.aspx?id=786dc6d7-766f-46df-ac4b-d6883597061f&target=profile"

            return (
              <ViewTeam
                {...props}
                key={key}
                team={team}
                viewChannels={props.details}
                viewDates={viewDates}
                onSaveTeam={props.onSaveTeam}
              />
            );
          })}
        </Masonry>
      </div>
    );
  } else {
    return null;
  }
};

export function ViewTeam(props) {
  const viewChannels = props.viewChannels;
  const [viewDates, setViewDates] = useState(props.viewDates);
  const [mouseOver, setMouseOver] = useState(false);
  const componentRef = useRef();

  const team = props.team ? props.team : {};

  function logo(show, icon) {
    if (!show) return null;
    var c = "ms-fontSize-28 ms-Icon ms-Icon--" + icon;
    return <i className={c} style={{ padding: "4px" }} aria-hidden="true"></i>;
  }

  // Notebook https://365adm.sharepoint.com/_layouts/groupstatus.aspx?id=786dc6d7-766f-46df-ac4b-d6883597061f&target=notebook"
  // Planner "https://tasks.office.com/365adm.onmicrosoft.com/en-US/Home/Group/786dc6d7-766f-46df-ac4b-d6883597061f"

  var conversationsHref = getHref(team);

  var details =
    team.data && team.data.details
      ? team.data.details
      : {
          displayName: team.title
            ? team.title
            : team.displayName
            ? team.displayName
            : team["@odata.type"],
        };
  var photo = team.data ? team.data.photo : null;
  var channels = team.data && team.data.channels ? team.data.channels : [];

  if (props.isPinned) {
    //const { width, height } = useContainerDimensions(componentRef,props.refreshCount)
    var width = componentRef.current
      ? componentRef.current.offsetParent.offsetWidth - 2
      : 0;
    var height = componentRef.current
      ? componentRef.current.offsetParent.offsetHeight - 2
      : 0;

    return (
      <div
        ref={componentRef}
        className="pinnedTile"
        style={{ position: "relative",pointer:"default" }}
        onClick={() => {
          if (!props.editMode) {
          
          }
        }}
      >
        <div
          style={{ position: "absolute", right: 4, top: 4, display: "flex" }}
        >
          <div>
            {logo(team.hasTeams, "TeamsLogo")}
            {logo(team.hasSharePoint, "SharepointLogo")}
            {logo(team.hasYammer, "YammerLogo")}
          </div>
        </div>
        <div
          style={{
            position: "absolute",
            right: 10,
            bottom: 4,
            display: "flex",
          }}
        >
          {" "}
          <div style={{ flexGrow: 1 }}>{team.visibility}</div>
        </div>
        <div
          style={{ display: "flex" }}
          onMouseEnter={() => {
            setMouseOver(true);
          }}
          onMouseLeave={() => {
            setMouseOver(false);
          }}
          style={{
            width,
            height,
            xborder: "1px dashed red",
            xborderRadius: "20px",
          }}
        >
          <div style={{ display: "flex" }}>
            <div>
              {props.editMode && mouseOver && (
                <div style={{ position: "absolute", right: "0" }}>
                  {" "}
                  <span
                    className="ms-Icon ms-Icon--Delete tiledelete"
                    onClick={() => {
                      if (props.onRemovePin) {
                        props.onRemovePin(props.pinId);
                      }
                    }}
                  ></span>
                </div>
              )}
{!props.editMode && mouseOver && (
                <div style={{ position: "absolute", right: "0" }}>
                  {" "}
                  <span
                    className="ms-Icon ms-Icon--OpenInNewWindow tileopen "
                    onClick={() => {
                      window.open(conversationsHref);
                    }}
                  ></span>
                </div>
              )}


              {/* {width}x{height} - {props.width}x{props.height} {props.refreshCounter} */}
              {photo && (
                <div className="" style={{ minWidth: "48px" }}>
                  <img
                    className="pinnedTile2"
                    src={photo}
                    style={{
                      height: "48px",
                      width: "48px",
                    }}
                  ></img>
                </div>
              )}
              {team.tile && team.tile.icon && (
                <div
                  className="pinnedTile2"
                  style={{
                    backgroundColor: team.tile.color,
                    minWidth: "48px",
                  }}
                >
                  <img
                    src={team.tile.icon}
                    style={{
                      margin: "12px",
                      height: "24px",
                      width: "24px",
                    }}
                  ></img>
                </div>
              )}
            </div>
            <div
              className="ms-font-xl "
              style={{
                padding: "8px",
                marginRight: "20px",
                lineHeight: "32px",
                flexGrow: 1,
              }}
            >
              {team.displayName}
              {team.channel && (
                <div className="channelListing">
                  <ul style={{ xdisplay: "none", marginTop: "10px" }}>
                    <TeamChannel
                      team={team}
                      viewMode
                      channel={team.channel}
                      details={true}
                    />
                  </ul>{" "}
                </div>
              )}
            </div>
          </div>
          {mouseOver && (
            <ListTags
              indexLaunzhContexts={props.indexLaunzhContexts}
              href={conversationsHref}
            />
          )}
        </div>
      </div>
    );
  }
  if (team.tile) {
    return (
      <div
        onMouseEnter={() => {
          setMouseOver(true);
        }}
        onMouseLeave={() => {
          setMouseOver(false);
        }}
        style={{
          paddingLeft: "0px",
          margin: "10px",
          fontSize: "16px",
          xdisplay: "flex",
          border: "1px solid #dddddd",
          minWidth: "290px",
          maxWidth: "290px",
        }}
      >
        <span
          onClick={() => {
            trace(EVENT.TILECLICKED, {
              "tile-key": team.tile.key,
              href: team.tile.externalArticle,
            });
          }}
        >
          <a
            className="TeamLink"
            href={team.tile.externalArticle}
            rel="noopener noreferrer"
            target="_blank"
          >
            <div>
              <div
                style={{
                  minWidth: "288px",
                  paddingLeft: "0px",
                  paddingBottom: "0px",
                  position: "relative",
                }}
              >
                {mouseOver && (
                  <div
                    style={{
                      position: "absolute",
                      right: "10px",
                      top: "10px",
                      zIndex: "1",
                      backgroundColor: "#ffffffcc",
                    }}
                  >
                    <ActionButton
                      styles={{ fontSize: "24px" }}
                      iconProps={{ iconName: "Save" }}
                      ariaLabel="Save"
                      onClick={(event) => {
                        if (props.onSaveTeam) {
                          event.preventDefault(); // Let's stop this event.
                          event.stopPropagation(); // Really this time.
                          props.onSaveTeam(team);
                        }
                      }}
                    >
                      Save Tile
                    </ActionButton>
                  </div>
                )}
                {team.tile.icon && (
                  <div
                    className="teamTitleBar"
                    style={{
                      backgroundColor: team.tile.color,
                      marginTop: "-20px",
                      minWidth: "280px",
                    }}
                  >
                    <img
                      src={team.tile.icon}
                      style={{
                        margin: "70px",
                        height: "140px",
                        width: "140px",
                      }}
                    ></img>
                  </div>
                )}
              </div>
              <div className="teamTitleBar" style={{ minWidth: "280px" }}>
                <div
                  className="ms-font-xl "
                  style={{
                    padding: "8px",
                    lineHeight: "32px",
                    textAlign: "center",
                  }}
                >
                  <Highlight
                    text={team.tile.title}
                    filter={props.filter}
                    style={props.highlightStyle}
                  />
                </div>
              </div>
            </div>

            {team.tile.inShort && (
              <div
                className="ms-font-m"
                style={{
                  fontStyle: "italic",
                  marginTop: "10px",
                  paddingLeft: "20px",
                  paddingRight: "20px",
                  margin: "0px",
                }}
              >
                <div style={{ fontStyle: "italic", marginTop: "8px" }}>
                  <Highlight
                    text={team.tile.inShort}
                    filter={props.filter}
                    style={props.highlightStyle}
                  />
                </div>
              </div>
            )}
          </a>
        </span>
      </div>
    );
  }

  return (
    <div
      onMouseEnter={() => {
        setMouseOver(true);
      }}
      onMouseLeave={() => {
        setMouseOver(false);
      }}
      style={{
        paddingLeft: "0px",
        margin: "10px",
        fontSize: "16px",
        xdisplay: "flex",
        border: "1px solid #dddddd",
        minWidth: "290px",
        maxWidth: "290px",
      }}
    >
    
        <div>
          <div
            style={{
              minWidth: "290px",
              paddingLeft: "0px",
              paddingBottom: "0px",
              position: "relative",
            }}
          >
            {mouseOver && (
              <div
                style={{
                  position: "absolute",
                  right: "10px",
                  top: "10px",
                  zIndex: "1",
                  backgroundColor: "#ffffffcc",
                }}
              >
                <ActionButton
                  styles={{ fontSize: "24px" }}
                  iconProps={{ iconName: "Save" }}
                  ariaLabel="Save"
                  onClick={(event) => {
                    if (props.onSaveTeam) {
                      event.preventDefault(); // Let's stop this event.
                      event.stopPropagation(); // Really this time.
                      props.onSaveTeam(team);
                    }
                  }}
                >
                  Save Tile
                </ActionButton>
              </div>
            )}
            {photo && (
              <img
                className="teamImage"
                src={photo}
                style={{
                  height: "288px",
                  width: "288px",
                  marginLeft: "0px",
                }}
              ></img>
            )}
          </div>
          <div className="teamTitleBar" style={{ minWidth: "280px" }}>
            <div
              className="ms-font-xl "
              style={{
                padding: "8px",
                lineHeight: "32px",
                textAlign: "center",
              }}
            >
              <Highlight
                text={details.displayName}
                filter={props.filter}
                style={props.highlightStyle}
              />
            </div>
          </div>
          <div style={{ border: "0px solid #dddddd", paddingBottom: "4px" }}>
            <div
              style={{
                display: "flex",
                marginRight: "10px",
                marginLeft: "10px",
                marginTop: "10px",
              }}
            >
              <div style={{ flexGrow: 1 }}>{team.visibility}</div>
              <div>
                {logo(team.hasTeams, "TeamsLogo")}
                {logo(team.hasSharePoint, "SharepointLogo")}
                {logo(team.hasYammer, "YammerLogo")}
              </div>
            </div>

            {/* {team.data.endPoints && 
              team.data.endPoints.map((endPoint, key) => {
             // if (endPoint.providerName==="Yammer") debugger
              return <div key={key}><a target="_blank" href={endPoint.uri}>{endPoint.providerName} ({endPoint.capability})</a></div>;
              })} */}
          </div>
        </div>
        {details.displayName !== details.description && (
          <div
            className="ms-font-m"
            style={{
              fontStyle: "italic",
              marginTop: "10px",
              paddingLeft: "20px",
              paddingRight: "20px",
              margin: "0px",
            }}
          >
            {details.description}
          </div>
        )}
        {viewChannels && (
          <div className="ms-font-l ms-fontWeight-bold">
            {channels.length > 1 && (
              <div style={{ marginTop: "10px", marginLeft: "20px" }}>
                Channels
              </div>
            )}
            <div className="channelListing">
              <ul style={{ xdisplay: "none", marginTop: "10px" }}>
                {channels.map((channel, key) => {
                  if (channel.displayName === "$General") {
                    return null;
                  }
                  return (
                    <TeamChannel
                      {...props}
                      noMessages
                      key={key}
                      channel={channel}
                      details={details}
                    />
                  );
                })}
              </ul>
            </div>
          </div>
        )}
        <div>
          {viewDates && (
            <div style={{ display: "xflex" }}>
              {team.expirationDateTime && (
                <div style={{ flexGrow: 1 }}>
                  Expires in {moment(team.expirationDateTime).fromNow(true)}
                </div>
              )}
              {/* {team.renewedDateTime && (<div style={{ flexGrow: 1 }}>
                     Renewed {moment(team.renewedDateTime).toNow(true)} a go
          </div>)} */}
            </div>
          )}{" "}
        </div>
 
    </div>
  );
}
function Messages(props) {
  if (!props.messages ) return null;
 
  
  return (
    <div style={{ overflow: "auto", height: "200px" }}>
      {_.orderBy(props.messages,["createdDateTime"],["desc"]).map((message, key) => {
        var show = message.body && message.body.content && message.body.content !== "" ? true:false
        
        if (!show) return null

if (message.from && message.from.user && message.from.user.displayName && _.startsWith(message.from.user.displayName,"orgid:")){
  return null
}
        
        var html =
        {__html: message.body.content};
        return (
          <div key={key}>
            <a href={message.webUrl} target="_blank" style={{color:"inherit",textDecoration:"none"}}>
             

              {message.from && message.from.user && (
                <div>
                <div><b>{message.from.user.displayName} ({moment( message.createdDateTime).fromNow()})</b></div>
              
                </div>
              )}
                <div dangerouslySetInnerHTML={html}>
            
              </div>
            </a>
          </div>
        );
      })}
    </div>
  );
}
function TeamChannel(props) {
  useEffect(() => {
    async function load() {
      if (!props.noMessages &&  props.team && props.channel) {
        var _messages = await readTeamChannelMessages(
          props.team.id,
          props.channel.id
        ).catch(() => {});
        setMessages(_messages);
      }
    }
    load();
  }, [props.team,props.channel]);
  const [mouseOver, setMouseOver] = useState(false);
  const [messages, setMessages] = useState(null);

  var channel = props.channel ? props.channel : {};

  var details = props.details ? props.details : {};
  var team = props.team ? props.team : {};

  return (
    <li
      key={props.key}
      className={`ms-font-m ${props.viewMode ? "" : "teamchannelSelectMode"} `}
      onMouseEnter={() => {
        setMouseOver(true);
      }}
      onMouseLeave={() => {
        setMouseOver(false);
      }}
    >
      <div
        style={{
          color: channel.membershipType === "private" ? "red" : "#000000",
        }}
      >
        {mouseOver && !props.viewMode && (
          <div
            style={{
              position: "relative",
            }}
          >
            <div
              style={{
                position: "absolute",
                right: "4px",
                top: "4px",
                zIndex: "1",
              }}
            >
              <IconButton
                styles={{ fontSize: "24px" }}
                iconProps={{ iconName: "Save" }}
                ariaLabel="Save"
                onClick={(event) => {
                  if (props.onSaveChannel) {
                    event.preventDefault(); // Let's stop this event.
                    event.stopPropagation(); // Really this time.
                    props.onSaveChannel(team, channel);
                  }
                }}
              />
            </div>{" "}
          </div>
        )}
        <Highlight
          text={channel.displayName}
          filter={props.filter}
          style={props.highlightStyle}
        />
      </div>
      {channel.description && (
        <div style={{ fontSize: "smaller", fontStyle: "italic" }}>
          <Highlight
            text={channel.description}
            filter={props.filter}
            style={props.highlightStyle}
          />
        </div>
      )}
      {channel.tabs && (
        <div className="channelTabs">
          <ul>
            <li>
              {" "}
              <span
                onClick={() => {
                  trace(EVENT.TABCLICKED, {
                    "team-name": details.displayName,
                    "channel-name": channel.displayName,
                    href: channel.filesLink,
                  });
                }}
              >
                <a href={channel.generalLink} target="_blank">
                  General
                </a>
              </span>
            </li>
            <li>
              <span
                onClick={() => {
                  trace(EVENT.TABCLICKED, {
                    "team-name": details.displayName,
                    "channel-name": channel.displayName,
                    href: channel.filesLink,
                  });
                }}
              >
                <a href={channel.filesLink} target="_blank">
                  Files
                </a>
              </span>
            </li>
            {_.sortBy(channel.tabs, ["sortOrderIndex"]).map((tab, ix) => {
              return (
                <li key={ix}>
                  <span
                    onClick={() => {
                      trace(EVENT.TABCLICKED, {
                        "team-name": details.displayName,
                        "channel-name": channel.displayName,
                        "tab-name": tab.displayName,
                        href: channel.filesLink,
                      });
                    }}
                  >
                    <a
                      href={
                        tab.configuration && tab.configuration.websiteUrl
                          ? tab.configuration.websiteUrl
                          : tab.webUrl
                      }
                      target="_blank"
                    >
                      <Highlight
                        text={decodeURIComponent(tab.displayName)}
                        filter={props.filter}
                        style={props.highlightStyle}
                      />
                    </a>
                  </span>
                </li>
              );
            })}
          </ul>
        </div>
      )}
      {messages && <Messages messages={messages} />}
    </li>
  );
}

export function ListTags(props) {
  //debugger
  if (!props.indexLaunzhContexts) return null;

  if (!props.indexLaunzhContexts[props.href]) return null;
  return (
    <div style={{ margin: "8px", position: "absolute", bottom: "0", right: 0 }}>
      {props.indexLaunzhContexts[props.href].map((tag, key) => {
        return (
          <span
            onClick={() => {
              window.location.href = `/board/${tag.name}`;
            }}
            className="tag"
            key={key}
            style={{ color: tag.color, backgroundColor: tag.backgroundColor }}
          >
            #{tag.name}
          </span>
        );
      })}
    </div>
  );
}
