

import _ from "lodash";
import Axios from "axios";



export function getLicenseForUPN(upn) {
  return new Promise((resolve, reject) => {
    
  if (!upn) return reject("Mssing upn")
  
  
  var s =  _.split(upn,"@")
  if (s.length<2) return reject("Mssing domain part")
  var domain = _.toLower( s[1])


  Axios
  .get(`https://blob.jumpto365.com/licenses/${domain}.license.json`)
  .then((response) => {
    resolve(response.data);
  })
  .catch((e) => {
    reject(e);
  });
})
}
