export function saveCache(memberships) {
  return new Promise((resolve, reject) => {
    var membershipIndex = [];
    var queue = 0;
    memberships.forEach((membership) => {
      membershipIndex.push(membership.id);
      queue++;

      saveOneDriveFile(OURPATH, cachedFileName(membership.id), membership).then(
        () => {
          queue--;
          if (queue < 1) {
            saveOneDriveFile(OURPATH, "memberships.json", membershipIndex)
              .then(() => {
                resolve(membershipIndex);
              })
              .catch((e) => {
                reject(e);
              });
          }
        }
      );
    });
  });
}

function cachedFileName(id) {
  return "membership." + id + ".json";
}

export function readCache() {
  return new Promise(async (resolve, reject) => {
    var membershipIndex = await getOneDriveFile(
      OURPATH,
      "memberships.json"
    ).catch((e) => {
      reject(e);
    });
    if (!membershipIndex) return;
    var queue = 0;
    var memberships = [];
    membershipIndex.forEach((id) => {
      queue++;
      getOneDriveFile(OURPATH, cachedFileName(id))
        .then((data) => {
          memberships.push(data);
          queue--;
          if (queue < 1) {
            resolve(memberships);
          }
        })
        .catch((e) => {
          reject(e);
        });
    });
  });
}

/**
 * Verifies access to all endpoints used for the LaunchPad app
 *
 * @param {*} setCheckMe Callback accepting a string - "OK" or an error
 * @param {*} setCheckMyExtention  Callback accepting a string - "OK" or an error
 * @param {*} setCheckOneDrive  Callback accepting a string - "OK" or an error
 * @param {*} setCheckOneDriveFiles  Callback accepting a string - "OK" or an error
 * @param {*} setCheckMemberships  Callback accepting a string - "OK" or an error
 * @param {*} setCheckTeam  Callback accepting a string - "OK" or an error
 */
export async function verifyAccessForLanuchPad(
  setCheckMe,
  setCheckMyExtention,
  setCheckOneDrive,
  setCheckOneDriveFiles,
  setCheckMemberships,
  setCheckTeam
) {
  me()
    .then((m) => {
      setCheckMe("OK");
    })
    .catch((e) => {
      setCheckMe(e.message);
    });

  var extention = await saveMyExtention(extentionName, {
    hello: "world",
  }).catch((e) => {
    setCheckMyExtention(e.message);
  });
  if (extention || extention === "") {
    setCheckMyExtention("OK");
  }

  getOneDriveFolder(OURPATH)
    .then((r) => {
      setCheckOneDrive("OK");
      checkAccessToConfig();
    })
    .catch(async (e) => {
      var f = await getOneDriveFolder("Apps").catch((e) => {});
      if (f) {
        var child = await createOneDriveFolderChild(
          "Apps",
          "jumpto365.com"
        ).catch((e) => {
          setCheckOneDrive(e.message);
        });
        if (child) {
          setCheckOneDrive("OK");
          checkAccessToConfig();
        }
      } else {
        var child = await createOneDriveFolder("Apps").catch((e) => {
          setCheckOneDrive(e.message);
        });
        child = await createOneDriveFolderChild("Apps", "jumpto365.com").catch(
          (e) => {
            setCheckOneDrive(e.message);
          }
        );
        if (child) {
          setCheckOneDrive("OK");
          checkAccessToConfig();
        }
      }
    });

  myGroups()
    .then((memberships) => {
      setCheckMemberships("OK");
      var teamId;
      memberships.forEach((member) => {
        if (member.displayName) {
          if (!teamId) {
            teamId = member.id;
          }
        }
      });
      if (teamId) {
        readTeam(teamId)
          .then((m) => {
            setCheckTeam("OK");
          })
          .catch((e) => {
            setCheckTeam(e.message);
          });
      } else {
        setCheckTeam("Didn't find any Microsoft Teams memberships");
      }
    })
    .catch((e) => {
      setCheckMemberships(e.message);
    });

  async function checkAccessToConfig() {
    var settings = await getOneDriveFile(OURPATH, OURCONFIGFILENAME).catch(
      async (e) => {
        var file = await saveOneDriveFile(OURPATH, OURCONFIGFILENAME, {
          version: projectInfo.version,
        }).catch((e) => {
          setCheckOneDriveFiles(e.message);
        });
        if (file) {
          setCheckOneDriveFiles("OK");
        }
      }
    );
    if (settings) {
      setCheckOneDriveFiles("OK");
      checkAccessToContexts();
    }
  }
}

export async function checkAccessToContexts() {
  await createOneDriveFolderChild("Apps", "jumpto365.com");
  await createOneDriveFolderChild(OURPATH, BOARDS);
}

export function saveBoard(memberships, name) {
  return new Promise(async (resolve, reject) => {
    await createOneDriveFolderChild(BOARDSPATH, name);
    var currentBoardPath = BOARDSPATH + "/" + name;
    var membershipIndex = [];
    var queue = 0;
    memberships.forEach((membership) => {
      membershipIndex.push(membership.id);
      queue++;

      saveOneDriveFile(
        currentBoardPath,
        cachedFileName(membership.id),
        membership
      ).then(() => {
        queue--;
        if (queue < 1) {
          saveOneDriveFile(
            currentBoardPath,
            "memberships.json",
            membershipIndex
          )
            .then(() => {
              resolve(membershipIndex);
            })
            .catch((e) => {
              reject(e);
            });
        }
      });
    });
  });
}

export function readBoard(name) {
  return new Promise(async (resolve, reject) => {
    var currentBoardPath = BOARDSPATH + "/" + name;
    var membershipIndex = await getOneDriveFile(
      currentBoardPath,
      "memberships.json"
    ).catch((e) => {
      reject(e);
    });
    if (!membershipIndex) return;
    var queue = 0;
    var memberships = [];
    membershipIndex.forEach((id) => {
      queue++;
      getOneDriveFile(currentBoardPath, cachedFileName(id))
        .then((data) => {
          memberships.push(data);
          queue--;
          if (queue < 1) {
            resolve(memberships);
          }
        })
        .catch((e) => {
          reject(e);
        });
    });
  });
}

var buffer = {
  layouts: { pending: false },
};

function delaywriteLayouts() {
  setTimeout(() => {
    console.log("delaywriteLayouts");
    if (!buffer.layouts.pending) {
      return;
    }
    if (buffer.layouts.writing) {
      return delaywriteLayouts();
    }
    writeLayouts(
      buffer.layouts.folder,
      buffer.layouts.file,
      buffer.layouts.data
    );
  }, 2000);
}

export function writeLayouts(folder, file, data) {
  // if (data.myTeams.length === 0) {
  //   console.log("Ignoring writeLayouts with no teams")
  //   return}
  return new Promise(async (resolve, reject) => {
    console.log("writeLayouts");
    if (buffer && buffer.layouts && buffer.layouts.writing) {
      if (_.isEqual(buffer.layouts.data, data)) {
        console.log("writeLayouts no changes");
        return resolve();
      }
      buffer.layouts.pending = true;
      buffer.layouts.data = data;
      buffer.layouts.file = file;
      buffer.layouts.folder = folder;
      delaywriteLayouts();
      return resolve();
    }

    try {
      buffer.layouts.writing = true;
      buffer.layouts.current = data;

      await saveOneDriveFile(folder, file, data);
      buildIndexOfLaunzhBoards()
      resolve();
    } catch (error) {
      console.log("writeLayouts > error", error);
      return reject(error);
    } finally {
      buffer.layouts.writing = false;
    }
  });
}

export function readLayouts(folder, file) {
  return getOneDriveFile(folder, file);
}

export function readBoardFiles() {
  return getOneDriveFolderContent(BOARDSPATH);
}

export function readBoards() {
  return new Promise(async (resolve, reject) => {
    var files = await getOneDriveFolderContent(BOARDSPATH).catch((e) => {
      reject(e);
    });
    if (!files) return; // rejected in catch - hopefully (-;

    var pending = 0;
    var results = [];
    files.forEach((file) => {
      pending++;
      Axios.get(file["@microsoft.graph.downloadUrl"])
        .then((result) => {
          pending--;
          results.push({
            file,
            content: result.data,
          });
          if (pending < 1) {
            resolve(results);
          }
        })
        .catch((e) => {
          reject(e);
        });
    });
  });
}
export function teamsLink(href) {
  if (href) {
    var part1 = href.split("https://teams.microsoft.com/l/team/");
    var part2 = part1[1].split("/conversations?");
    var part3 = href.split("groupId=");
    var part4 = part3[1].split("&");
    var part5 = href.split("tenantId=");
    var threadId = part2[0];
    var groupId = part4[0];
    var tenantId = part5[1];
    var conversationsHref = `https://teams.microsoft.com/_?tenantId=${tenantId}#/conversations/unknown?groupId=${groupId}&threadId=${threadId}&ctx=channel`;
    var filesHref = `https://teams.microsoft.com/_?tenantId=${tenantId}#/files/unknown?groupId=${groupId}&threadId=${threadId}&ctx=channel`;
    return conversationsHref;
  } else {
    return null;
  }
}
export function teamsLink2(href) {
  if (href) {
    var part1 = href.split("https://teams.microsoft.com/l/channel/");
    var part2 = part1[1].split("/conversations?");
    var part3 = href.split("groupId=");
    var part4 = part3[1].split("&");
    var part5 = href.split("tenantId=");
    var threadId = part2[0];
    var groupId = part4[0];
    var tenantId = part5[1];
    var conversationsHref = `https://teams.microsoft.com/_?tenantId=${tenantId}#/conversations/unknown?groupId=${groupId}&threadId=${threadId}&ctx=channel`;
    var filesHref = `https://teams.microsoft.com/_?tenantId=${tenantId}#/files/unknown?groupId=${groupId}&threadId=${threadId}&ctx=channel`;
    return conversationsHref;
  } else {
    return null;
  }
}
export function getHref(team){
  var href =
  team && team.data && team.data.details ? team.data.details.webUrl : "";
  if (team.data ) {
    var uniqueEndPoints = _.groupBy(
      team.data.endPoints ? team.data.endPoints : [],
      (endPoint) => {
        return endPoint.providerName;
      }
    );
  }
    var conversationsHref =
      team && team.tile
        ? team.tile.jumpto
          ? team.tile.jumpto
          : team.tile.externalArticle
        : uniqueEndPoints["Yammer"]
        ? uniqueEndPoints["Yammer"][0].uri
        : teamsLink(href);

    return conversationsHref
}


/**
 * Read all context for a given user and build an index of where a given URL is found
 */
export function buildIndexOfLaunzhBoards() {
  return new Promise(async (resolve, reject) => {
    var index = {}
    var ctxs = await readBoards()
    
    ctxs.forEach(ctx => {
      try {
        
     var fullname = ctx.file.name.split(".")
     var name = fullname ? fullname[0] : ctx.file.name
  


      ctx.content.myTeams.forEach(team => {
        
        var href = getHref(team)
        if (!index[href]){
          index[href] = [{name,color:"#000000",backgroundColor:"#b3e6a9"}]
        }else{
          index[href].push({name,color:"#000000",backgroundColor:"#b3e6a9"})
        }
        
      });
    } catch (error) {
        console.log("indexLaunzhContexts",error)
    }
    });
    await saveOneDriveFile(OURPATH, BOARDINDEXFILENAME , index);
  });
}

export function readIndexOfLaunzhBoards() {
  return getOneDriveFile(OURPATH, BOARDINDEXFILENAME);
}