import React, { Component, useState, useEffect } from "react";

import "../App.css";
import "../css/normalize.css";
import "../css/webflow.css";
import "../css/swimlane-process-map.webflow.css";

import _ from "lodash";
import {

  readBoards,
} from "../helpers/LaunchPad";
import { DragableDetailsList } from "../components/DragableDetailsList";
import { CommandBar } from "@fluentui/react";

import config from "../config";




function inIframe() {
  try {
    return window.self !== window.top;
  } catch (e) {
    return true;
  }
}
export default function Boards(props) {
  const [boards, setBoards] = useState([]);
  const [boardItems, setBoardItems] = useState([]);
  const inFrame = inIframe();

  useEffect(() => {
    async function run() {
      var _boards = await readBoards()
      setBoards(_boards);
      var boardItems = (_boards?_boards:[]).map(board=>{
        
        return {
          name:board.file.name,
          color:"#000000",backgroundColor:"#b3e6a9",
          items: board.content.myTeams.length + " items"
        }
      })
      
      setBoardItems(boardItems)
      // await verifyAccessForLanuchPad(setCheckMe, setCheckMyExtention, setCheckOneDrive, setCheckOneDriveFiles, setCheckMemberships, setCheckTeam);
    }
    run();
  }, []);

  return (
    <div>
       {!inFrame && config.components.header}
      <CommandBar />
  <DragableDetailsList items={boardItems}/>
    </div>
  );
}

function LaunzhContext(props) {
  if (!props.launzhContext) return null
  
  return <div>
{props.launzhContext.file.webUrl}

  </div>;
}





