function InteractionRequired(props){
  const [showWarning, setShowWarning] = useState(true);
 
  if (!props.interactionRequired) return null
  return  <div style={{position:"fixed",width:"100vw",zIndex:10}}>
  <MessageBar
  messageBarType={MessageBarType.error}
  isMultiline={false}
  onDismiss={() => {
    setShowWarning(false);
  }}
  dismissButtonAriaLabel="Close"
  actions={
    <div>
      {!inIframe() && 
      <MessageBarButton
        onClick={() => {
        
          selectAccount();
          
        }}
      >
        {" "}

       Sign in
      </MessageBarButton>
      }

      {inIframe() && 
      <>
      <a href="#" target="_blank">Open in new tab and sign in</a>
      <MessageBarButton
        onClick={() => {
        
          window.location.reload();
          
        }}
      >
        {" "}

       Reload
      </MessageBarButton>
      </>
      }
    </div>
  }
>
 We need to sign you in
</MessageBar></div>
}
function getUserAndLicense() {
  return new Promise(async (resolve, reject) => {
    var auth = await authenticate(config, (search, search2,interactionRequired) => {

      return resolve({interactionRequired})
      var errorMessage =
        search && search.error_description
          ? search.error_description
          : search2 && search2.error_description
          ? search2 && search2.error_description
          : "Unknown error";
      showException({ errorMessage });
      // this is where you can send a signal to a host that intercati ons is required
    }).catch((error) => {
      reject(error);
    });
    if (!auth) return;

    var license = await getLicenseForUPN(auth.user.userPrincipalName).catch(
      (e) => {
        console.log("getLicenseForUPN", e);
      }
    );

    who(auth.user.userPrincipalName);
    const key = "HASLANDED";

    var landed = sessionStorage.getItem(key);
    if (!landed) {
      trace(EVENT.LANDING);
      sessionStorage.setItem(key, "1");
    }
    resolve({auth, license});
  });
}

function LaunzhApp() {
  const [auth, setAuth] = useState(null);

  const [license, setLicense] = useState(null);
  const [interactionRequired, setInteractionRequired] = useState(false);
  // let { userContext, dispatch } = React.useContext(UserContext);
  useEffect(() => {
 
      getUserAndLicense()
      .then((result) => {
        
        if (result.interactionRequired){
          setInteractionRequired(true)
        }else{
          setAuth(result.auth);
          setLicense(result.license);
        }
        
       
      })
      .catch((error) => {
        console.log("getUserAndLicense", error);
      });
    
  }, [])


  return (
    <Customizer {...FluentCustomizations}>
      <Fabric theme={x}>
        <ErrorBoundary>
        <InteractionRequired interactionRequired={interactionRequired}/>
          <Router>
            <Onboard path="/check" />
            <Boards path="/boards" />
            <GotoDefault path="/" />
            <App auth={auth} path="/board/:board" signOut={signOut} />
            <GetAdminConsented path="/getadminconsent" />
            <AdminConsented path="/adminconsented" />
            <SignedOut path="/signedout" />
            <SignOut path="/signout" />
            <SelectAccount path="/selectaccount" />
            <ShowLicense path="/license" license={license} />
            <NoLicense path="/no-license" />
            <LicenseExpired path="/license-expired" license={license} />
            <Debug path="/debug" />
            <NoPage default />
          </Router>
        </ErrorBoundary>
      </Fabric>
    </Customizer>
  );
}

async function start() {
  ReactDOM.render(<LaunzhApp />, document.getElementById("root"));
}
if (window.Office) window.Office.initialize = () =>{
  console.log("Office running")
}
start();
