import React from "react";
import _ from "lodash";
import "./BreakPointSelector.css"
export default function BreakPointSelector(props) {
  var breakPoints = props && props.breakpoints ? _.keys(props.breakpoints) : [];

  return (
    <div className="breakpoints" style={{ display: "flex" }}>
      {breakPoints.map((breakPoint, key) => {
        return <div key={key} className={`breakpoint ${breakPoint===props.selected?"selected":""}`} 
        
        onClick={()=>{
            
            if (props.onSelect){
                props.onSelect(breakPoint)
            }
        }}
        >{breakPoint}</div>;
      })}
    </div>
  );
}
