import React from "react";
import { Link } from "office-ui-fabric-react/lib/Link";
import {
  DetailsList,
  Selection,
  IColumn,
  buildColumns,
  IColumnReorderOptions,
  IDragDropEvents,
  IDragDropContext,
} from "office-ui-fabric-react/lib/DetailsList";
import { MarqueeSelection } from "office-ui-fabric-react/lib/MarqueeSelection";
import { getTheme, mergeStyles } from "office-ui-fabric-react/lib/Styling";
import _ from "lodash";
import {
  CommandBar,
  ICommandBarItemProps,
} from "office-ui-fabric-react/lib/CommandBar";
import { IButtonProps } from "office-ui-fabric-react/lib/Button";

const overflowProps = { ariaLabel: "More commands" };
const theme = getTheme();
const margin = "0 30px 20px 0";
const dragEnterClass = mergeStyles({
  backgroundColor: theme.palette.neutralLight,
});
const controlWrapperClass = mergeStyles({
  display: "flex",
  flexWrap: "wrap",
});
const textFieldStyles = {
  root: { margin: margin },
  fieldGroup: { maxWidth: "100px" },
};
const togglesStyles = { root: { margin } };

export class DragableDetailsList extends React.Component {
  _selection;

  _dragDropEvents;
  _draggedItem;
  _draggedIndex;

  componentDidUpdate(prevProps, prevState) {
    if (!_.isEqual(prevProps.items, this.props.items)) {
      this.setState({
        items: this.props.items,
        columns: buildColumns(this.props.items, true),
      });
    }
  }
  cmdItems = () => {
    var items = [];

    switch (this._selection.getSelectedCount()) {
      case 0:
        items = [
          {
            key: "newItem",
            text: "New",
            cacheKey: "myCacheKey", // changing this key will invalidate this item's cache
            iconProps: { iconName: "Add" },
            //   subMenuProps: {
            //     items: [
            //       {
            //         key: 'emailMessage',
            //         text: 'Email message',
            //         iconProps: { iconName: 'Mail' },
            //         ['data-automation-id']: 'newEmailButton', // optional
            //       },
            //       {
            //         key: 'calendarEvent',
            //         text: 'Calendar event',
            //         iconProps: { iconName: 'Calendar' },
            //       },
            //     ],
            //   },
          },
          {
            key: "upload",
            text: "Upload",
            iconProps: { iconName: "Upload" },
            href: "https://developer.microsoft.com/en-us/fluentui",
          }
          
        ];
        break;
      case 1:
        items = [
          {
            key: "share",
            text: "Share",

            iconProps: { iconName: "Share" },
            onClick: () => console.log("Share"),
          },
          {
            key: "download",
            text: "Download",
   
            iconProps: { iconName: "Download" },
            onClick: () => console.log("Download"),
          },
         
          {
            key: "delete",
            text: "Delete",
 
            iconProps: { iconName: "Delete" },
            onClick: () => console.log("Delete"),
          },
        ];
        break;
      default:
        items = [
          {
            key: "share",
            text: "Share",

            iconProps: { iconName: "Share" },
            onClick: () => console.log("Share"),
          },
          
          {
            key: "download",
            text: "Download",

            iconProps: { iconName: "Download" },
            onClick: () => console.log("Download"),
          },
          {
            key: "delete",
            text: "Delete",
 
            iconProps: { iconName: "Delete" },
            onClick: () => console.log("Delete"),
          },
        ];
        break;
    }

    return items;
  };
  constructor(props) {
    super(props);
    var that = this;
    this._selection = new Selection({
      selectionMode: 2,
      onSelectionChanged: (x, y) => {
        console.log(this._draggedIndex);
        this.setState({ _items: this.cmdItems() });
      },
    });

    this._dragDropEvents = this._getDragDropEvents();
    this._draggedIndex = -1;
    //const items = [] // createListItems(10, 0);

    this.state = {
      items: props.items,
      columns: buildColumns(props.items, true),
      isColumnReorderEnabled: false,
      frozenColumnCountFromStart: "0",
      frozenColumnCountFromEnd: "0",
      _items: this.cmdItems(),

      _overflowItems: [
        {
          key: "move",
          text: "Move to...",
          onClick: () => console.log("Move to"),
          iconProps: { iconName: "MoveToFolder" },
        },
        {
          key: "copy",
          text: "Copy to...",
          onClick: () => console.log("Copy to"),
          iconProps: { iconName: "Copy" },
        },
        {
          key: "rename",
          text: "Rename...",
          onClick: () => console.log("Rename"),
          iconProps: { iconName: "Edit" },
        },
      ],

      _farItems: [
        // {
        //   key: "tile",
        //   text: "Grid view",
        //   // This needs an ariaLabel since it's icon-only
        //   ariaLabel: "Grid view",
        //   iconOnly: true,
        //   iconProps: { iconName: "Tiles" },
        //   onClick: () => console.log("Tiles"),
        // },
        {
          key: "info",
          text: "Info",
          // This needs an ariaLabel since it's icon-only
          ariaLabel: "Info",
          iconOnly: true,
          iconProps: { iconName: "Info" },
          onClick: () => console.log("Info"),
        },
      ],
    };
  }

  render() {
    const {
      items,
      columns,
      isColumnReorderEnabled,
      frozenColumnCountFromStart,
      frozenColumnCountFromEnd,
      _items,
      _overflowItems,
      _farItems,
    } = this.state;

    return (
      <div>
        <CommandBar
          items={_items}
          overflowItems={_overflowItems}
          overflowButtonProps={overflowProps}
          farItems={_farItems}
          ariaLabel="Use left and right arrow keys to navigate between commands"
        />
        <MarqueeSelection selection={this._selection}>
          <DetailsList
            setKey="items"
            items={items}
            columns={columns}
            selection={this._selection}
            selectionPreservedOnEmptyClick={true}
            onItemInvoked={this._onItemInvoked}
            onRenderItemColumn={this._onRenderItemColumn}
            dragDropEvents={this._dragDropEvents}
            columnReorderOptions={
              this.state.isColumnReorderEnabled
                ? this._getColumnReorderOptions()
                : undefined
            }
            ariaLabelForSelectionColumn="Toggle selection"
            ariaLabelForSelectAllCheckbox="Toggle selection for all items"
            checkButtonAriaLabel="Row checkbox"
          />
        </MarqueeSelection>
      </div>
    );
  }

  _handleColumnReorder = (draggedIndex, targetIndex) => {
    const draggedItems = this.state.columns[draggedIndex];
    const newColumns = [...this.state.columns];

    // insert before the dropped item
    newColumns.splice(draggedIndex, 1);
    newColumns.splice(targetIndex, 0, draggedItems);
    this.setState({ columns: newColumns });
  };

  _getColumnReorderOptions() {
    return {
      frozenColumnCountFromStart: parseInt(
        this.state.frozenColumnCountFromStart,
        10
      ),
      frozenColumnCountFromEnd: parseInt(
        this.state.frozenColumnCountFromEnd,
        10
      ),
      handleColumnReorder: this._handleColumnReorder,
    };
  }

  _validateNumber(value) {
    return isNaN(Number(value))
      ? `The value should be a number, actual is ${value}.`
      : "";
  }

  _onChangeStartCountText = (event, text) => {
    this.setState({ frozenColumnCountFromStart: text });
  };

  _onChangeEndCountText = (event, text) => {
    this.setState({ frozenColumnCountFromEnd: text });
  };

  _onChangeColumnReorderEnabled = (ev, checked) => {
    this.setState({ isColumnReorderEnabled: checked });
  };

  _getDragDropEvents() {
    return {
      canDrop: (dropContext, dragContext) => {
        return true;
      },
      canDrag: (item) => {
        return true;
      },
      onDragEnter: (item, event) => {
        // return string is the css classes that will be added to the entering element.
        return dragEnterClass;
      },
      onDragLeave: (item, event) => {
        return;
      },
      onDrop: (item, event) => {
        if (this._draggedItem) {
          this._insertBeforeItem(item);
        }
      },
      onDragStart: (item, itemIndex, selectedItems, event) => {
        this._draggedItem = item;
        this._draggedIndex = itemIndex;
      },
      onDragEnd: (item, event) => {
        this._draggedItem = undefined;
        this._draggedIndex = -1;
      },
    };
  }

  _onItemInvoked = (item) => {
    alert(`Item invoked: ${item.name}`);
  };

  _onRenderItemColumn = (item, index, column) => {
    const key = column.key;
    if (key === "name") {
      return <Link data-selection-invoke={true}>{item[key]}</Link>;
    }

    return String(item[key]);
  };

  _insertBeforeItem(item) {
    const draggedItems = this._selection.isIndexSelected(this._draggedIndex)
      ? this._selection.getSelection()
      : [this._draggedItem];

    const insertIndex = this.state.items.indexOf(item);
    const items = this.state.items.filter(
      (itm) => draggedItems.indexOf(itm) === -1
    );

    items.splice(insertIndex, 0, ...draggedItems);

    this.setState({ items });
  }
}
