export function who(upn) {
  if (!window.amplitude) return;
  if (!upn) return;
  var amp =  window.amplitude.getInstance()
  if (!amp) return
  
  amp.setUserId(upn);

  var s = upn.split("@");
  if (s.length === 2) {
    amp.setDomain(s[1]);
  }
}

export function trace(event, details) {
  if (!window.amplitude) return;
  var amp =  window.amplitude.getInstance()
  if (!amp) return
  amp.logEvent(event, details);
  console.log("trace",event, details);
}
